import { io, Socket } from 'socket.io-client';
import { uuidv4 } from '@sillytavern/scripts/utils';

type ConnectOptions = {
  url: string;
  namespace?: string;
  token?: string;
  stream?: boolean;
  methodDefault?: 'generate' | 'generateRaw';
};

type OpenAIChatMessage = {
  role: 'system' | 'user' | 'assistant';
  content: string;
};

type ChatCompletionsCreatePayload = {
  /**  ID */
  id?: string;
  /**  Tavern  */
  model?: string;
  /** OpenAI  user  user_input */
  messages?: OpenAIChatMessage[];
  /**  connectRelay  */
  stream?: boolean;
  /**  generate  generateRaw generate */
  method?: 'generate' | 'generateRaw';
  /** Tavern  */
  tavern?: {
    overrides?: any;
    injects?: any[];
    max_chat_history?: 'all' | number;
    custom_api?: any;
    /**  generation_id */
    generation_id?: string;
    /**  user_input messages  */
    user_input?: string;
    /** generateRaw  */
    ordered_prompts?: any[];
    /**  method === 'generateRaw' */
    use_raw?: boolean;
  };
  /**  user_input */
  user_input?: string;
};

const reqToGen = new Map<string, string>();
let currentSocket: Socket | null = null;
let currentStreamDefault = true;
let currentMethodDefault: 'generate' | 'generateRaw' = 'generate';
let _connected = false;
const statusWatchers: ((s: boolean) => void)[] = [];

function notifyStatus(s: boolean) {
  _connected = s;
  console.info("[SocketIO/notifyStatus] ''", { connected: s });
  for (const fn of statusWatchers) {
    try {
      fn(s);
    } catch (e) {
      console.error("[SocketIO/notifyStatus] ''", e);
    }
  }
}

export function onRelayStatus(cb: (s: boolean) => void) {
  statusWatchers.push(cb);
  console.info("[SocketIO/onRelayStatus] ''", { watchersCount: statusWatchers.length });
  cb(_connected);
}

export function isConnected() {
  return _connected;
}

export async function connectRelay(opts: ConnectOptions) {
  const ns = (opts.namespace || '').startsWith('/') ? opts.namespace! : `/${opts.namespace || ''}`;
  const url = `${opts.url}${ns}`;
  //  stream  true
  const hadStreamOpt = typeof opts.stream === 'boolean';
  if (hadStreamOpt) {
    currentStreamDefault = opts.stream === true;
  }
  if (opts.methodDefault) {
    currentMethodDefault = opts.methodDefault;
  }
  console.info("[SocketIO/connectRelay] ''", {
    url,
    namespace: ns,
    tokenPresent: !!opts.token,
    streamDefault: currentStreamDefault,
    streamOverride: hadStreamOpt,
    methodDefault: currentMethodDefault,
  });
  if (currentSocket) {
    try {
      console.info("[SocketIO/connectRelay] ''");
      currentSocket.disconnect();
    } catch (e) {
      console.error("[SocketIO/connectRelay] ''", e);
    }
    currentSocket = null;
  }
  const s = io(url, {
    transports: ['websocket'],
    auth: opts.token ? { token: opts.token } : undefined,
    autoConnect: true,
    reconnection: true,
    reconnectionDelayMax: 10000,
  });
  currentSocket = s;
  console.info("[SocketIO/connectRelay] 'Socket '");

  s.on('connect', () => {
    console.info("[SocketIO/connectRelay] ''");
    notifyStatus(true);
    try {
      void pushRegexesToServer();
    } catch (e) {
      /* ignore */
    }
  });
  s.on('disconnect', reason => {
    console.warn("[SocketIO/connectRelay] ''", { reason });
    notifyStatus(false);
  });

  // OpenAI chat.completions.create 
  s.on('openai.chat.completions.create', (payload: ChatCompletionsCreatePayload) => {
    console.info("[SocketIO/connectRelay] ''", payload);
    void handleChatCompletionsCreate(payload);
  });

  // / req_id -> generation_id
  s.on('openai.abort', (data: { id?: string; req_id?: string }) => {
    console.info("[SocketIO/connectRelay] ''", data);
    const reqId = data?.id || data?.req_id;
    if (!reqId) return;
    const genId = reqToGen.get(reqId);
    const TH: any = (window as any).TavernHelper;
    if (genId && TH?.stopGenerateById) {
      try {
        TH.stopGenerateById(genId);
        console.info("[SocketIO/connectRelay] ''", { generation_id: genId });
      } catch (e) {
        console.warn("[SocketIO/connectRelay] ''", e);
      }
    }
  });
}

export function disconnectRelay() {
  if (currentSocket) {
    try {
      console.info("[SocketIO/disconnectRelay] ''");
      currentSocket.disconnect();
    } catch (e) {
      console.error("[SocketIO/disconnectRelay] ''", e);
    }
  }
  currentSocket = null;
  notifyStatus(false);
}

export async function pushRegexesToServer() {
  const socket = currentSocket;
  if (!socket) return;
  try {
    const w: any = window as any;
    const TH = w.TavernHelper;
    if (TH?.getTavernRegexes) {
      const regexes = TH.getTavernRegexes({ scope: 'all', enable_state: 'enabled' });
      socket.emit('tavern.regexes.update', Array.isArray(regexes) ? regexes : []);
      console.info("[SocketIO/pushRegexesToServer] ''", { count: (regexes || []).length });
    }
  } catch (e) {
    console.warn("[SocketIO/pushRegexesToServer] ''", e);
  }
}

function lastUserInputFromMessages(messages?: OpenAIChatMessage[]): string {
  if (!messages || messages.length === 0) return '';
  for (let i = messages.length - 1; i >= 0; i--) {
    if (messages[i].role === 'user') {
      return (messages[i] as any).content ?? '';
    }
  }
  return (messages[messages.length - 1] as any)?.content ?? '';
}

function toNonStreamResponse(params: { reqId: string; model?: string; content: string }) {
  const id = `chatcmpl_${params.reqId}`;
  const now = Math.floor(Date.now() / 1000);
  return {
    id,
    object: 'chat.completion',
    created: now,
    model: params.model || 'sillytavern',
    choices: [
      {
        index: 0,
        message: { role: 'assistant', content: params.content },
        finish_reason: 'stop',
      },
    ],
  };
}

function toStreamChunk(params: { reqId: string; delta: string; model?: string; done?: boolean }) {
  const id = `chatcmpl_${params.reqId}`;
  const now = Math.floor(Date.now() / 1000);
  return {
    id,
    object: 'chat.completion.chunk',
    created: now,
    model: params.model || 'sillytavern',
    choices: [
      params.done
        ? { index: 0, delta: {}, finish_reason: 'stop' }
        : { index: 0, delta: { content: params.delta }, finish_reason: null },
    ],
  };
}

async function handleChatCompletionsCreate(payload: ChatCompletionsCreatePayload) {
  const socket = currentSocket;
  if (!socket) {
    console.warn("[SocketIO/handleChatCompletionsCreate] ' socket'");
    return;
  }

  const reqId = payload.id || uuidv4();
  const genId = payload.tavern?.generation_id || uuidv4();
  reqToGen.set(reqId, genId);

  const stream = payload.stream ?? currentStreamDefault ?? true;

  const user_input = payload.user_input ?? payload.tavern?.user_input ?? lastUserInputFromMessages(payload.messages);

  //  generate  generateRawpayload.method > tavern.use_raw/ordered_prompts > 
  const method: 'generate' | 'generateRaw' = (() => {
    if (payload.method === 'generate' || payload.method === 'generateRaw') return payload.method;
    if (payload.tavern?.use_raw === true || Array.isArray(payload.tavern?.ordered_prompts)) return 'generateRaw';
    return currentMethodDefault;
  })();

  // 
  const config: any = {
    user_input,
    should_stream: stream,
    overrides: payload.tavern?.overrides,
    injects: payload.tavern?.injects,
    max_chat_history: payload.tavern?.max_chat_history,
    custom_api: payload.tavern?.custom_api,
    generation_id: genId,
  };
  if (method === 'generateRaw') {
    config.ordered_prompts = payload.tavern?.ordered_prompts;
  }

  const TH: any = (window as any).TavernHelper;
  if (!TH?.[method]) {
    console.error("[SocketIO/handleChatCompletionsCreate] 'TavernHelper '", { method });
    socket.emit('openai.chat.completions.error', {
      id: reqId,
      error: { message: `TavernHelper.${method} `, type: 'internal_error' },
    });
    return;
  }

  console.info("[SocketIO/handleChatCompletionsCreate] ''", {
    reqId,
    genId,
    stream,
    method,
    model: payload.model,
    user_input_preview: (user_input || '').slice(0, 120),
  });

  let full = '';
  const onInc = (incremental: string, id: string) => {
    if (id !== genId) return;
    try {
      full += incremental || '';
      const chunk = toStreamChunk({
        reqId,
        delta: incremental || '',
        model: payload.model,
      });
      socket.emit('openai.chat.completions.chunk', { id: reqId, data: chunk });
      console.debug("[SocketIO/handleChatCompletionsCreate/onInc] ''", { id, len: (incremental || '').length });
    } catch (e) {
      console.error("[SocketIO/handleChatCompletionsCreate/onInc] ''", e);
    }
  };
  const onFull = (_fullText: string, id: string) => {
    if (id !== genId) return;
    console.info("[SocketIO/handleChatCompletionsCreate/onFull] ''", { id, len: (_fullText || '').length });
    // 
  };
  const onEnd = (text: string, id: string) => {
    if (id !== genId) return;
    try {
      const finalText = text ?? full;
      console.info("[SocketIO/handleChatCompletionsCreate/onEnd] ''", {
        id,
        stream,
        final_len: (finalText || '').length,
      });
      if (stream) {
        const done = toStreamChunk({
          reqId,
          delta: '',
          model: payload.model,
          done: true,
        });
        socket.emit('openai.chat.completions.chunk', { id: reqId, data: done });
        socket.emit('openai.chat.completions.done', { id: reqId });
      } else {
        const resp = toNonStreamResponse({
          reqId,
          model: payload.model,
          content: finalText,
        });
        socket.emit('openai.chat.completions.result', { id: reqId, data: resp });
      }
    } finally {
      cleanup();
      reqToGen.delete(reqId);
      console.info("[SocketIO/handleChatCompletionsCreate] ''", { reqId });
    }
  };

  const pmListener = (ev: MessageEvent) => {
    const data = (ev as any)?.data;
    if (!data || data.source !== 'socketio-extension') return;
    const t = data.type;
    const p = data.payload || {};
    try {
      console.debug("[SocketIO/handleChatCompletionsCreate/pmListener] ''", { type: t, id: p.id });
      if (t === 'STREAM_TOKEN_RECEIVED_INCREMENTALLY') {
        onInc(p.incremental_text, p.id);
      } else if (t === 'STREAM_TOKEN_RECEIVED_FULLY') {
        onFull(p.full_text, p.id);
      } else if (t === 'GENERATION_ENDED') {
        onEnd(p.text, p.id);
      }
    } catch (e) {
      console.error("[SocketIO/handleChatCompletionsCreate/pmListener] ''", e);
    }
  };

  const cleanup = () => {
    try {
      //  iframe 
      eventRemoveListener(iframe_events.STREAM_TOKEN_RECEIVED_INCREMENTALLY, onInc as any);
      eventRemoveListener(iframe_events.STREAM_TOKEN_RECEIVED_FULLY, onFull as any);
      eventRemoveListener(iframe_events.GENERATION_ENDED, onEnd as any);
    } catch (e) {
      /* ignore */
    } finally {
      try {
        window.removeEventListener('message', pmListener as any);
      } catch (e) {
        /* ignore */
      }
    }
  };

  //  token  postMessage 
  try {
    window.addEventListener('message', pmListener as any);
  } catch (e) {
    /* ignore */
  }
  try {
    //  iframe  iframe 
    eventOn(iframe_events.STREAM_TOKEN_RECEIVED_INCREMENTALLY, onInc as any);
    eventOn(iframe_events.STREAM_TOKEN_RECEIVED_FULLY, onFull as any);
    eventOn(iframe_events.GENERATION_ENDED, onEnd as any);
  } catch (e) {
    /* ignore */
  }

  // 
  socket.emit('openai.chat.completions.accepted', {
    id: reqId,
    generation_id: genId,
  });
  console.info("[SocketIO/handleChatCompletionsCreate] ''", { reqId, generation_id: genId });

  try {
    await TH[method](config);
  } catch (e: any) {
    cleanup();
    console.error("[SocketIO/handleChatCompletionsCreate] ''", e);
    socket.emit('openai.chat.completions.error', {
      id: reqId,
      error: { message: e?.message || String(e), type: 'generation_error' },
    });
    reqToGen.delete(reqId);
  }
}
